.h2 {
  position: relative;
  font-size: 48px;
  color: var(--white);
  text-transform: uppercase;
  line-height: 1.2;

  @include less(tabletL){
    font-size: 42px;
  }

  @include less(tabletS){
    font-size: 32px;
  }

  &:before {
    content: url("../img/title_before.png");
    position: absolute;
    left: 0;
    top: -55px;
  }
}

.h3 {
  font-size: 32px;
  color: var(--white);
  font-weight: bold;
  line-height: 1.2;

  @include less(tabletL){
    font-size: 26px;
  }

  @include less(tabletS){
    font-size: 24px;
  }
}

.p {
  font-size: 18px;
  color: var(--light);
  line-height: 1.5;
  font-weight: 300;
}
