* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  opacity: 1;
  transition: 1s linear;

  &.overflow-hidden {
    overflow: hidden;
  }

  &.invisible {
    opacity: 0 !important;
  }
}

main {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

a {
  color: unset;
  text-decoration: none;
}

input,
button {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;

  &:focus,
  &:hover {
    outline: none;
  }
}

button {
  user-select: none;
}

::selection {
  color: var(--lightBlue);
  background-color: rgba(0, 0, 0, 0.5);
}


// variables file must be at the top of the imports
@import "_responsive";
@import "_variables";
@import "_mixins";
@import "_fonts";
@import "_animations";
@import "_typography";
@import "Components";

// pages
@import "../sass-pages/index";
