// CONTAINERS
$k2: 2560px;
$desktop: 1920px;
$laptopXL: 1680px;
$laptopL: 1640px;
$laptopM: 1440px;
$laptopS: 1280px;
$tabletL: 1024px;
$tablet: 768px;
$tabletS: 600px;
$mobileL: 480px;
$mobileM: 375px;
$mobileS: 320px;

// traget - is your pixels
// device - is on which device it shoud look like
@function get-vw($target, $device) {
  $vw-context: ($device * 0.01);
  @return ($target/$vw-context) * 1vw;
}

@mixin more($size) {
  @if $size == k2 {
    @media (min-width: $k2) {
      @content;
    }
  } @else if $size == desktop {
    @media (min-width: $desktop) {
      @content;
    }
  } @else if $size == laptopL {
    @media (min-width: $laptopL) {
      @content;
    }
  } @else if $size == laptopM {
    @media (min-width: $laptopM) {
      @content;
    }
  } @else if $size == laptopS {
    @media (min-width: $laptopS) {
      @content;
    }
  } @else if $size == tabletL {
    @media (min-width: $tabletL) {
      @content;
    }
  } @else if $size == tablet {
    @media (min-width: $tablet) {
      @content;
    }
  } @else if $size == tabletS {
    @media (min-width: $tabletS) {
      @content;
    }
  } @else if $size == mobileL {
    @media (min-width: $mobileL) {
      @content;
    }
  } @else if $size == mobileM {
    @media (min-width: $mobileM) {
      @content;
    }
  } @else if $size == mobileS {
    @media (min-width: $mobileS) {
      @content;
    }
  }
}

