@mixin exactly($size) {
  @if $size == desktop {
    @media (min-width: $desktop) {
      @content;
    }
  } @else if $size == laptopL {
    @media (max-width: $desktop) and (min-width: $laptopL) {
      @content;
    }
  } @else if $size == laptopM {
    @media (max-width: $laptopL) and (min-width: $laptopM) {
      @content;
    }
  } @else if $size == laptopS {
    @media (max-width: $laptopM) and (min-width: $laptopS) {
