.main-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  pointer-events: none !important;
  user-select: none !important;

  display: flex;
  justify-content: space-between;

  & > div {
    width: 1px;
    background-color: var(--white);
    opacity: 0.1;
  }
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1180px;

  @include less(tabletL) {
    max-width: 100%;
    padding-left: 50px;
    padding-right: 50px;
  }

  @include less(tabletS) {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.btn {
  --btn-height: 75px;
  position: relative;
  width: 390px;
  height: var(--btn-height);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: none;
  outline: none;
  cursor: pointer;
  overflow: hidden;

  font-size: 22px;
  color: var(--blue);
  font-weight: bold;
  text-transform: uppercase;
  background-color: var(--yellow);

  transition: var(--basicTransition);
  transform-origin: center center;
  box-shadow: 0px 6px 4px #00000000;

  @include less(tabletL) {
    font-size: 20px;
    width: 300px;
    --btn-height: 60px;
  }

  @include less(tabletL) {
    width: 280px;
    --btn-height: 50px;
  }

  span {
    position: absolute;
    z-index: 2;
    height: var(--btn-height);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--basicTransition);

    &:nth-child(2) {
      top: 0;
      transition-delay: .08s;
      transform: scale(1);
    }

    &:nth-child(1) {
      top: 100%;
      transform: scale(1.2);
    }
  }

  &:hover {
    transform: rotateX(15deg);
    box-shadow: 0px 6px 8px #0000004f;

    span {
      //transition: 0s;

      &:nth-child(2) {
        top: -100%;
        transform: scale(0.6);
        opacity: 0;
      }

      &:nth-child(1) {
        top: 0;
        transform: scale(1);
      }
    }
  }
}
