.services {
  &-section {
    padding-top: 180px;
    background-color: var(--blue);
    padding-bottom: 190px;
    position: relative;

    @include less(tabletL) {
      padding-bottom: 200px;
    }
    @include less(tabletS) {
      padding-bottom: 120px;
    }
  }

  &-container {
    z-index: 1;
  }

  &-title {
  }

  &-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 110px 100px;
    padding: 0 100px;
    margin-top: 100px;

    @include less(tabletL) {
      grid-gap: 80px 50px;
      padding: 0 25px;
      margin-top: 80px;
    }

    @include less(tabletS) {
      grid-template-columns: 1fr;
      grid-gap: 50px 0;
      margin-top: 50px;
      padding: 0;
    }
  }

  &-item {
    * {
      transition: var(--longTransition);
    }

    &__icon {
      width: 55px;
    }

    &__title {
      margin-top: 15px;
      transition-delay: .2s;
    }

    &__p {
      margin-top: 50px;
      transition-delay: .4s;

      @include less(tabletS) {
        margin-top: 35px;
      }
    }
  }

  &-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 0;
    opacity: 0.05;
    pointer-events: none;
    user-select: none;

    @include less(tabletS) {
      bottom: auto;
      min-height: 100vh;

      svg {
        transform: scale(1.5);
        transform-origin: top;
      }
    }
  }
}

.lightpaper {
  &-section {
    position: relative;
    padding-bottom: 300px;
    z-index: 6;
  }

  &-container {
    padding-top: 40px;
    position: relative;
    display: grid;
    grid-template-columns: 495px 1fr;
    grid-gap: 100px;
    align-items: center;
    justify-content: space-between;
    min-height: 310px;

    @include less(tabletL) {
      grid-template-columns: 1fr;
    }

    &:before {
      content: '';
      background-color: var(--lightBlue);
      position: absolute;
      top: 0;
      left: -100%;
      bottom: 0;
      right: 0;
      transform: scaleX(0);
      transform-origin: left;
      transition: var(--longTransition) ease-out;
    }


    &.animate-active {
      &:before {
        transform: scaleX(1);
      }
    }
  }

  &-text {
    position: relative;
    z-index: 1;
    height: 100%;
    display: grid;
    grid-template-areas: 'title . .'
    '. . p'
    'btn btn btn';
    grid-template-columns: auto auto auto;

    @include less(tabletL) {
      order: 2;
      grid-template-areas: 'title .'
    'p p'
    'btn btn';
      grid-template-columns: 200px auto;
    }
    @include less(tabletS) {
      grid-template-columns: 190px auto;
      grid-template-areas: 'title .'
    'p .'
    'btn btn';
    }

    &__doc {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 0;
      overflow: hidden;
      pointer-events: none;
      user-select: none;

      @include less(tabletL) {
        width: 200px;
      }

      img {
        transform: translateY(15%);
      }
    }
  }


  &-title {
    grid-area: title;
  }

  &-p {
    grid-area: p;
    font-size: 24px;
    font-weight: 300;
    color: var(--white);
    line-height: 1.4;

    transition-delay: .4s;

    @include less(tabletL) {
      max-width: 300px;
      margin-top: 30px;
    }

    @include less(tabletS) {
      font-size: 16px;
      max-width: 100%;
    }
  }

  &-btn-wrapp {
    grid-area: btn;
    align-self: flex-end;
    justify-self: flex-end;
    transform: translateY(50%);

    opacity: 0;
    pointer-events: none;
    transition: var(--longTransition);

    @include less(tabletS) {
      justify-self: center;
    }

    &.animate-active {
      opacity: 1;
      pointer-events: all;
    }
  }

  &-btn {
  }

  &-img {
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%;
    pointer-events: none;

    img {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0%, -20%) rotate(30deg);
      opacity: 0;
      transition: var(--longTransition);
      transition-delay: .8s;

      @include less(tabletL) {
        width: 450px;
      }

      @include less(tabletS) {
        transform: translate(40%, -20%) rotate(20deg);
        width: 360px;
        max-width: unset;
      }
    }

    &.animate-active {
      img {
        transform: translate(0%, -50%) rotate(0deg);
        opacity: 1;

        @include less(tabletS) {
          transform: translate(40%, -40%) rotate(0deg);
        }
      }
    }
  }
}
